export default function formatButton(blockString, tertiaryColour) {
  //if the block contans a # means it is one of the sites pages
  // there don' open in a new tab
  let target = blockString.includes("target") ? 'target="_blank"' : "";

  return (
    '<div class="text-center row"><a class="flat-butt text-center ' +
    fetchAlignment(blockString) +
    '"' +
    'style="max-width: 560px;' +
    fetchStyles(blockString, tertiaryColour) +
    '" ' +
    target +
    " " +
    fetchHref(blockString) +
    '" ><span style="padding-left: 10px;"> ' +
    fetchText(blockString) +
    "</span>" +
    '</a></div">'
  );
}

function fetchHref(blockString) {
  var patternURL = /(?<=href=")(.*?)(?=">)/g;
  return blockString.match(patternURL) != null
    ? 'href="' + blockString.match(patternURL)[0] + '"'
    : "";
}

function fetchText(blockString) {
  let textPattern = /(?<=<a class="wp-block-button__link.*?>)(.*?)(?=<\/a>)/g;
  return blockString.match(textPattern) != null
    ? blockString.match(textPattern)[0]
    : "";
}

function fetchStyles(blockString, tertiaryColour) {
  let pattern = /(?<=style=")(.*?)(?=")/g;

  let styles =
    blockString.match(pattern) != null ? blockString.match(pattern)[0] : "";

  console.log(!styles.includes("background-color"));

  if (!styles.includes("background-color")) {
    console.log(styles + " background-color: " + tertiaryColour + ";");
    return styles + "; background-color: " + tertiaryColour + ";";
  } else {
    return styles;
  }
}

function fetchAlignment(blockString) {
  if (blockString.includes("alignleft")) {
    return "alignleft";
  } else if (blockString.includes("alignright")) {
    return "alignright";
  } else {
    return "aligncenter";
  }
}
