export default function formatImage(blockString) {
  let src = fetchSource(blockString);
  let width = fetchWidth(blockString);
  let height = fetchHeight(blockString);
  let alignment = fetchAlignment(blockString);
  let caption = fetchCaption(blockString);
  let circular = fetchCircular(blockString);

  width = checkWidth(width, blockString);
  height = checkHeight(width, blockString);

  return (
    "<div class='d-flex flex-column " +
    alignment +
    "'>" +
    "<div class='p-2'>" +
    '<img class="' +
    '" ' +
    src +
    "style='" +
    "max-width:" +
    width +
    "; " +
    "max-height:" +
    height +
    ";" +
    circular +
    "'>" +
    '</img">' +
    "</div>" +
    "<div class='p-2' style='text-align: center'>" +
    "<em>" +
    caption +
    "</em>" +
    "</div>" +
    "</div>"
  );
}

function fetchSource(blockStirng) {
  var patternURL = /(?<=src=")(.*?)(?=")/g;
  return blockStirng.match(patternURL) != null
    ? "src=" + blockStirng.match(patternURL)[0] + " "
    : "";
}

function fetchWidth(blockStirng) {
  var patternURL = /(?<=width=")(.*?)(?=")/g;
  return blockStirng.match(patternURL) != null
    ? blockStirng.match(patternURL)[0] + "px"
    : "";
}

function fetchHeight(blockStirng) {
  var patternURL = /(?<=height=")(.*?)(?=")/g;
  return blockStirng.match(patternURL) != null
    ? blockStirng.match(patternURL)[0] + "px"
    : "";
}

function fetchAlignment(blockStirng) {
  var patternURL = /(?<=align)(.*?)(?= )/g;
  return blockStirng.match(patternURL) != null
    ? "align" + blockStirng.match(patternURL)[0] + " "
    : "";
}

function fetchCaption(blockStirng) {
  var patternURL = /(?<=<figcaption>)(.*?)(?=<\/figcaption>)/g;
  return blockStirng.match(patternURL) != null
    ? blockStirng.match(patternURL)[0]
    : "";
}

function checkWidth(width, blockStirng) {
  if (width != "") return width;

  return imgSizeTypes(blockStirng);
}

function checkHeight(height, blockStirng) {
  if (height != "") return height;

  return imgSizeTypes(blockStirng);
}

function imgSizeTypes(blockStirng) {
  var patternURL = /(?<=size-)(.*?)(?=")/g;
  let sizeType =
    blockStirng.match(patternURL) != null
      ? blockStirng.match(patternURL)[0]
      : "";

  switch (sizeType) {
    case "thumbnail":
      return "25%";
    case "medium":
      return "50%";
    case "large":
      return "75%";
    case "full":
      return "100%";
    default:
      return "100%";
  }
}

function fetchCircular(blockStirng) {
  if (blockStirng.includes("is-style-circle-mask")) {
    return "clip-path: circle(" + 50 + "%" + " at center);";
  }

  return "";
}
